body {
	font-family: "Arial", sans-serif;
	font-weight: 700;
	font-size: 20px;
	color: #2f4f4f;
}

.people {
	--column-count: 2;
	max-width: 890px;
	margin: 0 auto;
	padding: 30px 0;
	box-sizing: border-box;
	display: grid;
	gap: 10px 30px;
	justify-content: center;
	grid-template-columns: repeat(var(--column-count), 1fr);

	@media (min-width: 1000px) {
		--column-count: 4;
	}

}

.person {
	position: relative;
	width: 100%;
	text-align: center;
	cursor: pointer;

	&::before {
		content: '';
		position: absolute;
		inset: 0;
		z-index: -1;
		width: 100%;
		height: 100%;
		border-radius: 10px;
	}

	&:hover {
		z-index: 1;

		&::before {
			transform: scale(1.2);
			background: #eee;
		}

		.person__extra {
			transform: scale(1);
		}

		.person__photo {
			transform: scale(1.09);
			transition: transform 1s;
		}

		.person__img {
			border-radius: 50px;
			transition: border-radius 1s;
		}

		.person__details {
			transform: scale(1.15) translateY(15%);
		}
	}

	&__photo {
		position: relative;
		height: 200px;
		margin-bottom: 8px;
		transition: border-radius 1s;
	}

	&__img {
		height: 200px;
		object-fit: contain;
		border-radius: 50%;
		overflow: hidden;
		transition: border-radius 1s;
	}

	&__rank {
		position: absolute;
		top: 50%;
		right: 10px;
		display: flex;
		justify-content: center;
		align-items: center;
		min-width: 32px;
		min-height: 32px;
		margin-right: 10px;
		font-size: 16px;
		text-align: center;
		color: #8b0000;
		background: #fff;
		border-radius: 50%;
		transform: translateY(-50%);
	}

	&__details {
		transition: transform 1s;
	}

	&__name {
		display: block;
		margin-bottom: 10px;
		transition: font-size 1s;
	}

	&__extra {
		transform: scale(0);
		font-weight: 400;
		font-size: 18px;
		color: #000;
		text-align: center;
	}

}